










import urls from '../../config/urls.js'
import {
    mapState
} from 'vuex'
export default {
  data() {
    return {
      title: 'Hello'
    }
  },
  // 发起网络请求建议使用 uni-app 的 onReady代替 vue 的 mounted。
  // 建议使用 uni-app 的 onLoad 代替 vue 的 created
  onLoad() {
    uni.showLoading({
    	title:'正在加载中...'
    })
    setTimeout(function () {
      uni.hideLoading()
    }, 3000);
    // this.getUserInfo()
    // this.getcities('guess')
    this.getcities('hot')
    this.getcities('group')
  },
  computed: {
    ...mapState(['userInfo'])
  },
  methods: {
    getUserInfo () {
      this.$flyio.get(urls.userInfo).then((res) => {
        console.log(res)
      })
    },
    getcities (flag) {
      this.$flyio.get(urls.cities + '?type=' + flag).then((res) => {
        console.log(res)
      })
    },
    toLogin () {
      uni.navigateTo({
        url: '/pages/login/login'
      })
    }
  }
}
